import React, { useState, useEffect } from "react";
import { auth } from "../firebaseconfig";
import login from "../img/login.svg";
import { createUserWithEmailAndPassword, signInWithEmailAndPassword } from "firebase/auth";
import swal from 'sweetalert'
import { useNavigate   } from 'react-router-dom'


function Login() {
  const historial = useNavigate();
  const [email, setEmail] = useState("");
  const [contraseña, setContraseña] = useState("");

  const Logup = () => {
    createUserWithEmailAndPassword(auth, email, contraseña)
      .then((userCredential) => {
        // Signed in
        const user = userCredential.user;
        swal("Usuario Registrado",user.email,"success")
        historial('/');
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
        swal(errorCode,errorMessage,"error")
      });
  };

  const Login = () => {
    signInWithEmailAndPassword(auth, email, contraseña)
      .then((userCredential) => {
        // Signed in
        const user = userCredential.user;
        swal("Login Exitoso!",user.email,"success");
        historial('/');
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
        swal(errorCode,errorMessage,"error")
      });
  };

  return (
    <div className="container-fluid text-center mt-5">
      <div
        className="row d-flex justify-content-center"
        style={{ position: "absolute", zIndex: "10", marginLeft: "10%" }}
      >
        <h1>Formulario de Registro</h1>
        <div className="col-lg-10">
          <input
            type="email"
            className="form-control mt-4"
            placeholder="Introduce tu email..."
            onChange={(e) => setEmail(e.target.value)}
          />
          <input
            type="password"
            className="form-control mt-4"
            placeholder="Introduce tu contraseña..."
            onChange={(e) => setContraseña(e.target.value)}
          />
          <button className="btn btn-dark btn-block mt-4" onClick={Logup}>Registrar</button>
          <button className="btn btn-info btn-block" onClick={Login}>Iniciar Sesion</button>
        </div>
      </div>
      <div
        style={{
          position: "absolute",
          zIndex: "1",
          float: "right",
          marginLeft: "45%",
          marginTop: "5%",
        }}
      >
        <img src={login} className="img-fluid" alt="" />
      </div>
    </div>
  );
}

export default Login;
