import React, { useState, useEffect } from 'react';
import {Link, useNavigate} from 'react-router-dom'
import { auth } from '../firebaseconfig';

function Navbar() {
    const historial = useNavigate();
    const [usuario, setUsuario] = useState(null);
    useEffect(()=>{
        auth.onAuthStateChanged((user)=> {
            if (user) setUsuario(user);
        })
    },[])
    const Logout = () => {
        setUsuario(null);
        auth.signOut();
        historial('/login');
    }

    return (  
        <div className="navbar navbar-expand-lg navbar-dark bg-dark">
            <div className="navbar-brand">
                    <img className='img-fluid' src="https://img.icons8.com/external-bearicons-outline-color-bearicons/34/000000/external-login-call-to-action-bearicons-outline-color-bearicons.png" alt="" />
                </div>
            <ul className='navbar-nav'>
                <li className='navbar-item'>
                    <Link className='nav-link' to='/'>Inicio</Link>
                </li> 
                { !usuario ?
                <li className='navbar-item'>
                    <Link className='nav-link' to='/login'>Login</Link>
                </li> : <span></span> }
                { usuario ? 
                <li className='navbar-item'>
                    <Link className='nav-link' to='/admin'>Admin</Link>
                </li> : <span></span> }
            </ul>
            { usuario ? 
                <button style={{marginLeft: "76%"}} className="btn btn-danger" onClick={Logout}>Cerrar Sesion</button> 
                : <span></span>
            }
        </div>
    );
}

export default Navbar;